.modal {
  z-index: 12;
  position: relative;
  top: 21%;
  bottom: auto;
  max-width: 80%;
  height: auto;
  margin: 0 auto 40px;
  padding: 40px;
  background: #fff;
  opacity: 0;
  box-shadow: 0 0 25px rgba(68, 60, 34, 0.3);
  transition: all .35s ease-in-out;

  &.fadeOut {
    opacity: 1;
    visibility: visible;
  }

  @media (max-width: 767px) {
    max-width: 96%;
    padding: 40px 10px 10px;
  }
}

.modal-overlay {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(35, 45, 53, .5);
  filter: alpha(opacity=80);
  cursor: pointer;
  transition: all .25s ease-in-out;

  &.fadeOut {
    opacity: 1;
    visibility: visible;
  }
}

.modal-open {
  overflow: hidden;

  @media not all and (hover: none){
    padding-right: 17px;
  }
}

.modal__close {
  z-index: 1;
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: color .25s ease-in-out;

  &:hover {
    color: gray;
  }
}

.modal__content {
  display: flex;
  flex-direction: column;
}
