body {
  opacity: 1;
  overflow-x: hidden;
}

html {
  background-color: #fff;
}

.site-wrap {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  &.site-wrap--overlay {
    position: relative;
    transition: transform .35s;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 3;
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(#000, .7);
    }
  }
}
